@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

.btn {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 143%;
  text-align: center;
  color: #2e3238;
  padding: 5px 12px;
  border: 1px solid #d5d8dd;
  border-radius: 4px;
  cursor: pointer;
  background-color: #ffffff;
  transition: 0.3s ease-in-out;
}
.btn--default:hover {
  border-color: #0078cf;
}
.btn--primary {
  background-color: #0078cf;
  color: #ffffff;
  border-color: #0078cf;
}
.btn--primary:hover {
  background-color: #025d9f;
  border-color: #025d9f;
}
.btn--tab {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  border: none;
  padding: 0;
  border-radius: 0;
}
.btn--tab:hover {
  background-color: #d5d8dd;
}
.btn--icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  position: relative;
  bottom: 0;
  left: 0;
}
.btn--icon:hover {
  bottom: 2px;
  left: 2px;
  box-shadow: -7px 7px 5px 0 rgba(0, 0, 0, 0.75);
}
.btn--icon:active {
  bottom: 0;
  left: 0;
  box-shadow: none;
}
.btn--remove {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 16px;
  height: 16px;
  border: none;
  padding: 0;
}
.btn--remove:hover {
  transform: scale(1.2);
}
@media (max-width: 767px) {
  .btn--sidebar {
    position: absolute;
    top: 0;
    left: 0;
  }
  .btn--sidebar svg {
    width: 20px;
    height: 20px;
  }
}

.custom-input {
  width: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #2e3238;
  outline: none;
  padding: 5px 0 5px 8px;
  border: 1px solid #d5d8dd;
  border-radius: 4px;
  background: #fcfcfd;
  transition: 0.3s ease-in-out;
}
.custom-input--calendar {
  padding-top: 4px;
  padding-bottom: 4px;
}
.custom-input:hover {
  border-color: #0078cf;
}
.custom-input::placeholder {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #abb1ba;
}
.custom-input[type=date]::-webkit-calendar-picker-indicator {
  display: none;
}

.header {
  background-color: #ffffff;
  color: #2e3238;
  border-bottom: 1px solid #d5d8dd;
}
.header__logo {
  flex-shrink: 0;
}
.header__wrapper {
  display: flex;
  align-items: center;
  padding: 12px 16px;
}
.header__search {
  width: 100%;
  max-width: 295px;
  margin-left: auto;
}
.header__search input {
  padding-right: 25px;
  background-repeat: no-repeat;
  background-position: right 8px center;
  background-size: 16px 16px;
  background-image: url("../img/svg/search.svg");
}
.header__profile {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-left: 16px;
  margin-right: 8px;
}
.header__profile span {
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
}
@media (max-width: 767px) {
  .header__logo, .header__search {
    display: none;
  }
  .header__profile {
    margin-left: auto;
    margin-right: 16px;
  }
  .header__profile img {
    width: 24px;
    height: 24px;
  }
  .header__profile span {
    display: none;
  }
}

.intro {
  background-color: #ffffff;
  color: #2e3238;
}
.intro__wrapper {
  display: flex;
  height: 100%;
}

.task {
  background-color: #ffffff;
  color: #2e3238;
  overflow: hidden;
}
.task__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  background-color: #ffffff;
}
.task__header.fixed {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.04);
}
.task__create {
  display: flex;
  align-items: center;
  gap: 16px;
}
.task__create span {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
}
.task__save {
  display: inline-flex;
  align-items: center;
  gap: 4px;
}
.task__wrapper {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 16px;
  height: calc(100vh - 120px);
  overflow: auto;
}
.task__title {
  display: inline-block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 133%;
  flex-shrink: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.task__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.task__column {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}
.task__item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.task__item--required span {
  display: flex;
  gap: 4px;
}
.task__item--required span::before {
  content: "*";
  display: block;
  font-weight: 600;
  font-size: 14px;
  line-height: 143%;
  color: #b21f00;
}
.task__item span {
  font-weight: 600;
  font-size: 14px;
  line-height: 143%;
}
.task__inner {
  display: flex;
  align-items: start;
  gap: 4px;
}
.task__select {
  width: 100%;
}
@media (max-width: 991px) {
  .task__column {
    grid-template-columns: 1fr;
  }
  .task__create {
    width: 100%;
    justify-content: space-between;
  }
  .task__create span {
    font-family: "Open Sans", "Arial", sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    text-transform: uppercase;
    color: #5c6470;
  }
  .task__save {
    display: none;
  }
  .task__title {
    font-size: 20px;
    line-height: 120%;
  }
}

.sidebar {
  display: flex;
  background-color: #ffffff;
  color: #2e3238;
  width: 100%;
  max-width: 336px;
}
.sidebar__tab {
  border-right: 1px solid #d5d8dd;
}
.sidebar__inner {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: #fcfcfd;
  border-right: 1px solid #d5d8dd;
}
.sidebar__filter {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 16px;
  border-bottom: 1px solid #d5d8dd;
  background-color: #ffffff;
}
.sidebar__filter form {
  width: 100%;
}
.sidebar__filter form input {
  padding-right: 25px;
  background-repeat: no-repeat;
  background-position: right 8px center;
  background-size: 16px 16px;
  background-image: url("../img/svg/filter.svg");
}
.sidebar__list {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 120px);
  overflow: auto;
}
.sidebar__link {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #2e3238;
  padding: 10px 16px;
  transition: 0.3s ease-in-out;
}
.sidebar__link:hover {
  background-color: #f2f2f2;
}
.sidebar__link::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: center;
  background-image: url("../img/svg/arrow.svg");
}
@media (max-width: 991px) {
  .sidebar {
    width: auto;
  }
  .sidebar__inner {
    display: none;
  }
}
@media (max-width: 767px) {
  .sidebar {
    width: 0;
  }
}

.select2-container--default .select2-selection--multiple {
  width: 100%;
  overflow: hidden;
  padding-bottom: 4px;
  padding-right: 30px;
  border-color: #d5d8dd;
}
.select2-container--default .select2-selection--multiple:hover {
  border-color: #0078cf;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
  margin-top: 4px;
  padding-left: 6px;
  padding-right: 25px;
  border: 1px solid #99d5ff;
  border-radius: 4px;
  background: #e5f4ff;
  white-space: normal;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__display {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 143%;
  color: #2e3238;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  width: fit-content;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
  left: auto;
  right: 5px;
  border: none;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove span {
  font-size: 21px;
  line-height: 100%;
  color: #2e3238;
}

.select2-custom {
  position: relative;
  width: 100%;
}

.select2-custom--default .select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
  display: none;
}

.select2-custom--default .select2-container--default .select2-selection--multiple .select2-selection__choice {
  padding-right: 3px;
}

.select2-custom .select2-search {
  display: none;
}

@media (max-width: 374px) {
  .select2-container--default .select2-selection--multiple {
    padding-right: 10px;
  }
  .select2-container--default .select2-selection--multiple .select2-selection__choice {
    max-width: 88%;
  }
}
